<template>
  <ion-content class="ion-padding">
    <div>
      <br>
      <center><ion-title size="large">Logistica</ion-title></center>  
      <br/>
        <div>
          <ion-row>
            <ion-col  sizeLg="4" sizeMd="4" sizeXs="12"></ion-col>
            <ion-col  sizeLg="4" sizeMd="4" sizeXs="12">
              <ion-input type="text"  v-model="first_name" placeholder="Ingrese el nombre"/>
              <ion-input type="text"  v-model="last_name" placeholder="Ingrese el apellido"/>
              <ion-input type="text"  v-model="email" placeholder="Ingrese el correo electrónico"/>
              <!--<ion-input type="text"  v-model="user_name" placeholder="Ingrese el usuario"/>-->
              <ion-input type="password"  v-model="password" placeholder="Ingrese la contreseña"/>
              <ion-input type="password"  v-model="password_confirmation" placeholder="Repite la contreseña"/>
               <br>
              <center>
                <ion-button color="primary" @click="login()">
                  Enviar
                </ion-button>
                <br>
                <ion-button color="primary" @click="redirect()">
                  Login
                </ion-button>
              </center>
              </ion-col>
            <ion-col  sizeLg="4" sizeMd="4" sizeXs="12"></ion-col>
          </ion-row>
        </div>
      </div>
  </ion-content>
</template>

<script>

import { IonButton, IonInput,IonCol,IonRow } from "@ionic/vue";
import axios from 'axios'
import jwtToken from "@/plugins/jwt/jwt-token";
import {mapActions, mapGetters} from "vuex";
import user from "@/plugins/jwt/user";
import toast from '@/plugins/toast'

export default {
  name: "login-",
  components: {
    IonInput,
    IonCol,
    IonRow,
    IonButton
  },
  data() {
    return {
      first_name : null,
      last_name : null,
      email : null,
      password : null,
      password_confirmation : null,
    };
  },
  computed : {
    ...mapGetters([
        'getUser'
    ]),
  },
  mounted(){
    console.log(this.getUser.id)
  },
  methods: {
    ...mapActions([
          'setAuthUser',
    ]),
    redirect(){
      location.href = "/login"
    },

    async login(){

      var loading = await toast.showLoading()

      loading.present();

      axios.post('/api/users?login=true',{
        first_name : this.first_name,
        last_name : this.last_name,
        email : this.email,
        password : this.password, 
        password_confirmation : this.password_confirmation, 
      })
      .then(res => {
        loading.dismiss()
        console.log(res)
        user.setUser(res.data.user)
        jwtToken.setToken(res.data.access_token);
        this.setAuthUser(res.data.user)
        toast.openToast("Registro exitoso","success",10000);
        location.href = "/user/envios"
      })
      .catch(error => { 
        loading.dismiss()
        console.log(error);
      });
    },
  }
};
</script>

<style scoped>

ion-input {
  background: rgba(63,81,181,.15);
  border-radius: 20px;
  padding: 5px 8px !important;
  margin: 10px 0;

}
ion-button{
 width: 150px;
}

</style>



